@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Pacifico);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

body {
  padding: 0 2em;
  font-family: Montserrat, sans-serif;
  color: #444;
  background: #eee;
}

.wakatime {
  display: flex;
  align-items: center;
}

.ctm-table {
  background: #34495E;
  color: #fff;
  border-radius: .4em;
  overflow: hidden;
  width: 300px;
  margin: 20px;
  display: inline-block;
}

.ctm-table tr {
  border-color: lighten(#34495E, 10%);
}

.ctm-table th, td {
  margin: .5em 1em;
  padding: 1em 1.5em;
  text-align: center;
}

.ctm-table th, td:before {
  color: #21d3ebcb;
  font-size: 1.2em;
}

.error-msg {
  color: #cf0909cb;
}

:root {
  --orange: #ff4a00;
}

.trello {
  display: inline-block;
}

.btn {
  background-color: #ff4a00;
  background-color: var(--orange);
  border-bottom: 5px solid #ff4a00;
  border-bottom: 5px solid var(--orange);
  text-shadow: 0px -2px #8b8684;
  padding: 10px 40px;
  margin: 0px 10px 10px 0px;
  border-radius: 10px;
  font-family: 'Pacifico', cursive;
  font-size: 25px;
  color: #FFF;
}

.btn:active {
  -webkit-transform: translate(0px,5px);
          transform: translate(0px,5px);
  border-bottom: 1px solid;
}

.btn:hover {
  cursor: pointer;
}

.msg {
  font-weight: bold;
  font-size: 20px;
}
